import { CodeSnippet } from '../../components/CodeSnippet/CodeSnippet.js';
import { Card, Typography } from 'antd';
import type { FC } from 'react';

const { Title, Text, Paragraph } = Typography;

type InstructionProps = {
  apiServer: string;
};

const Instruction: FC<InstructionProps> = ({ apiServer }) => {
  return (
    <Card title="Setup environment" style={{ width: '100%' }}>
      <Title level={5} style={{ marginTop: 0 }}>
        CLI Setup
      </Title>
      <Paragraph>Install the CodePush CLI</Paragraph>
      <CodeSnippet
        language="bash"
        code={`npm install code-push-cli@latest -g`}
      />
      <br />
      <Paragraph>Login to Revopush server</Paragraph>
      <Paragraph>
        This will launch a browser, asking you to authenticate with your GitHub.
        This will generate an access key that you need to copy/paste into the
        CLI (it will prompt you for it). You are now successfully authenticated
        and can safely close your browser window.
      </Paragraph>
      <CodeSnippet language="bash" code={`code-push login ${apiServer}`} />
      <br />
      <Title level={5} style={{ marginTop: 0 }}>
        Android configuration
      </Title>
      <Paragraph>
        Add the <Text code>CodePushServerUrl</Text> to the file{' '}
        <Text code>android/app/src/main/res/values/strings.xml</Text>I
      </Paragraph>
      <CodeSnippet
        language="xml"
        code={`<resources>
  <string moduleConfig="true" name="CodePushServerUrl">${apiServer}</string>
</resources>`}
      />
      <br />
      <Title level={5} style={{ marginTop: 0 }}>
        iOS configuration
      </Title>
      <Paragraph>
        Add the <Text code>CodePushServerURL</Text>to the file{' '}
        <Text code>ios/[ProjectName]/Info.plist</Text>
      </Paragraph>
      <CodeSnippet
        language="xml"
        code={`<key>CodePushServerURL</key>\n<string>${apiServer}</string>`}
      />
    </Card>
  );
};

export default Instruction;
